import PageContainer from '../components/PageContainer';
import Container from '../components/Cart/Container';
import Details from '../components/Cart/Details';
import ProductsList from '../components/Cart/ProductsList';

const Element = () => {
    return (
        <PageContainer>
            <Container>
                <Details />
                <ProductsList />
            </Container>
        </PageContainer>
    );
}

export default Element;