import ReactDOM from 'react-dom/client';
import App from './App';

const setTheme = (name: 'light' | 'dark' | 'telegram') => {
    const root = document.documentElement.style;

    switch (name) {
        case 'light':
            root.setProperty('--additional-text-color', 'rgba(130, 130, 130, 1)');
            root.setProperty('--secondary-text-color', 'rgba(70, 70, 70, 1)');
            root.setProperty('--primary-text-color', 'rgba(30, 30, 30, 1)');
            root.setProperty('--invert-text-color', 'rgba(220, 220, 220, 1)');
            root.setProperty('--absolute-text-color', 'rgba(0, 0, 0, 1)');

            root.setProperty('--body-background-color', 'rgba(240, 240, 240, 1)');
            root.setProperty('--container-background-color', 'rgba(255, 255, 255, 0)');
            root.setProperty('--header-background-color', 'rgba(235, 235, 235, 0.95)');
            root.setProperty('--footer-background-color', 'rgba(245, 245, 245, 1)');
            root.setProperty('--element-background-color', 'rgba(225, 225, 225, 1)');
            root.setProperty('--element-transparent-background-color', 'rgba(0, 0, 0, 0.02)');

            root.setProperty('--primary-color', 'rgba(35, 35, 35, 1)');
            break;

        case 'dark':
            root.setProperty('--additional-text-color', 'rgba(120, 120, 120, 1)');
            root.setProperty('--secondary-text-color', 'rgba(180, 180, 180, 1)');
            root.setProperty('--primary-text-color', 'rgba(220, 220, 220, 1)');
            root.setProperty('--invert-text-color', 'rgba(30, 30, 30, 1)');
            root.setProperty('--absolute-text-color', 'rgba(255, 255, 255, 1)');

            root.setProperty('--body-background-color', 'rgba(15, 15, 15, 1)');
            root.setProperty('--container-background-color', 'rgba(0, 0, 0, 0)');
            root.setProperty('--header-background-color', 'rgba(20, 20, 20, 0.95)');
            root.setProperty('--footer-background-color', 'rgba(10, 10, 10, 1)');
            root.setProperty('--element-background-color', 'rgba(20, 20, 20, 1)');
            root.setProperty('--element-transparent-background-color', 'rgba(255, 255, 255, 0.02)');

            root.setProperty('--primary-color', 'rgba(220, 220, 220, 1)');
            break;

        case 'telegram':
            break;
    }
};

const systemDark = window.matchMedia('(prefers-color-scheme: dark)');

setTheme(systemDark.matches ? 'dark' : 'light');

systemDark.addEventListener('change', (e) => {
    setTheme(e.matches ? 'dark' : 'light');
});

const root = ReactDOM.createRoot(document.getElementById('root') as HTMLElement);
root.render(<App />);
