import { useEffect } from 'react';
import $ from 'jquery';

import { useCart } from '../../contexts/CartContext';

import './Details.css';

const Element = () => {
    const { price } = useCart();

    useEffect(() => {
        $('.cart-details-payment-method').on('click', function() {
            $('.cart-details-payment-method').removeClass('selected');
            $(this).addClass('selected');
        })
    }, []);

    return (
        <div className="cart-details">
            <input className="cart-details-address" type='text' placeholder='Адрес' />
            <input className="cart-details-promocode" type='text' placeholder='Промокод' />
            <div className="cart-details-payment">
                <div className="cart-details-payment-method">
                    <i className="fa-solid fa-money-bill"></i>
                    <span>При получении</span>
                </div>
                <div className="cart-details-payment-method">
                    <i className="fa-solid fa-credit-card"></i>
                    <span>По карте</span>
                </div>
            </div>
            <div className="cart-details-order">
                <span>Сумма заказа</span>
                <div className='cart-details-order-price'>
                    <span className='cart-details-order-price-value'>{ price.value }</span>
                    <span className='cart-details-order-price-currency'>{ price.currency }</span>
                </div>
                <div className="cart-details-order-button">
                    <i className="fa-solid fa-truck"></i>
                    Оформить заказ
                </div>
            </div>
        </div>
    );
}

export default Element;