import { Product } from '../../types';
import { useCart } from "../../contexts/CartContext";

import './Product.css';

interface Props {
    product: Product;
}

const Element = ({ product }: Props) => {
    const { hasProduct, addProduct } = useCart();
    const exists = hasProduct(product);

    switch (product.kind) {
        case 'liquid': return (
            <div className={`catalog-product${exists ? ' added' : ''}`}>
                <img src={ product.preview } draggable="false" />
                <div className="catalog-product-panel">
                    <div className="catalog-product-panel-brand">
                        { product.brand }
                        <div className={`
                            catalog-product-panel-brand-strength
                            ${ product.strength.value <= 0 ? ' low' : ''}
                            ${ product.strength.value > 0 && product.strength.value < 40 ? ' default' : ''}
                            ${ product.strength.value >= 40 && product.strength.value < 60 ? ' strong' : ''}
                            ${ product.strength.value >= 60 ? ' insane' : ''}
                        `}>
                            <span>{ product.strength.value }</span>
                            <span className="catalog-product-panel-brand-strength-type">{ product.strength.type }</span>
                        </div>
                    </div>
                    <div className="catalog-product-panel-name">{ product.name }</div>
                    <div className="catalog-product-panel-price" onClick={() => { if (!exists) addProduct(product) }}>
                        <div className="catalog-product-panel-price-value">
                            <span>{ product.price.value }</span>
                            <span className="catalog-product-panel-price-value-currency">{ product.price.currency }</span>
                        </div>
                        <div className="catalog-product-panel-price-add_to_cart">
                            <i className={`fa-solid ${exists ? 'fa-check' : 'fa-plus'}`}></i>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Element;