import $ from 'jquery';
import { useEffect } from 'react';

import './Filters.css';

const Element = () => {
    useEffect(() => {
        $('.catalog-filters-type').on('click', function() {
            let already_selected = $(this).hasClass('selected');
            let already_applied = $(this).hasClass('applied');

            $('.catalog-filters-type').removeClass('selected');

            if ($('.catalog-filters-type.applied').length == 0) {
                $(this).addClass('selected');
            }

            if (!already_applied) {
                $(this).removeClass('applied');
            }

            if (already_selected) {
                $(this).removeClass('selected');
            }

            $('.catalog-filters-brand').removeClass('selected');
        });

        $('.catalog-filters-brand').on('click', function() {
            let already_selected = $(this).hasClass('selected');

            if ($('.catalog-filters-type.selected').length > 0 || $('.catalog-filters-type.aplied').length > 0) {
                $(this).addClass('selected');
            }

            if (already_selected) {
                $(this).removeClass('selected');
            }
        });

        $('.catalog-filters-actions-apply').on('click', function() {
            $('.catalog-filters-type.selected').addClass('applied');
            $('.catalog-filters-type.applied').removeClass('selected');
            $('.catalog-filters-brand.selected').addClass('applied');
            $('.catalog-filters-brand.applied').removeClass('selected');
        })

        $('.catalog-filters-actions-reset').on('click', function() {
            $('.catalog-filters-type.selected').removeClass('selected');
            $('.catalog-filters-type.applied').removeClass('applied');
            $('.catalog-filters-brand.selected').removeClass('selected');
            $('.catalog-filters-brand.applied').removeClass('applied');
        })
    }, []);

    return (
        <div className="catalog-filters">
            <div className="catalog-filters-type-list">
                <div className="catalog-filters-type">
                    <div className="catalog-filters-type-border"></div>
                </div>
                <div className="catalog-filters-type">
                    <div className="catalog-filters-type-border"></div>
                </div>
                <div className="catalog-filters-type">
                    <div className="catalog-filters-type-border"></div>
                </div>
                <div className="catalog-filters-type">
                    <div className="catalog-filters-type-border"></div>
                </div>
            </div>
            <div className="catalog-filters-brand-list">
                <div className="catalog-filters-brand">
                    <img src="https://optim.tildacdn.com/tild3236-6632-4466-b033-373339663035/-/cover/186x168/center/center/-/format/webp/7.jpg.webp" />
                    <div className="catalog-filters-brand-border"></div>
                </div>
                <div className="catalog-filters-brand">
                    <div className="catalog-filters-brand-border"></div>
                </div>
                <div className="catalog-filters-brand">
                    <div className="catalog-filters-brand-border"></div>
                </div>
                <div className="catalog-filters-brand">
                    <div className="catalog-filters-brand-border"></div>
                </div>
                <div className="catalog-filters-brand">
                    <div className="catalog-filters-brand-border"></div>
                </div>
                <div className="catalog-filters-brand">
                    <div className="catalog-filters-brand-border"></div>
                </div>
                <div className="catalog-filters-brand">
                    <div className="catalog-filters-brand-border"></div>
                </div>
            </div>
            <div className="catalog-filters-actions">
                <div className="catalog-filters-action catalog-filters-actions-apply">
                    <i className="fa-solid fa-filter"></i>
                    <span>Применить</span>
                </div>
                <div className="catalog-filters-action catalog-filters-actions-reset">
                    <i className="fa-solid fa-arrow-rotate-left"></i>
                    <span>Сбросить</span>
                </div>
            </div>
        </div>
    );
}

export default Element;