import { Product } from '../../types';
import { useCart } from "../../contexts/CartContext";

import './Product.css';

interface Props {
    product: Product;
}

const Element = ({ product }: Props) => {
    const { getProductQuantity, addProduct, removeProduct, removeFullProduct } = useCart();
    const quantity = getProductQuantity(product);

    switch (product.kind) {
        case 'liquid': return (
            <div className="cart-product">
                <div className="cart-product-preview">
                    <img src={ product.preview } draggable="false" />
                </div>
                <div className="cart-product-panel">
                    <div className="cart-product-panel-brand">
                        { product.brand }
                        <div className={`
                            cart-product-panel-brand-strength
                            ${ product.strength.value <= 0 ? ' low' : ''}
                            ${ product.strength.value > 0 && product.strength.value < 40 ? ' default' : ''}
                            ${ product.strength.value >= 40 && product.strength.value < 60 ? ' strong' : ''}
                            ${ product.strength.value >= 60 ? ' insane' : ''}
                        `}>
                            <span>{ product.strength.value }</span>
                            <span className="cart-product-panel-brand-strength-type">{ product.strength.type }</span>
                        </div>
                    </div>
                    <div className="cart-product-panel-name">{ product.name }</div>
                    <div className="cart-product-panel-price">
                        <div className="cart-product-panel-price-value">
                            <span>{ (product.price.value) * quantity }</span>
                            <span className="cart-product-panel-price-value-currency">{ product.price.currency }</span>
                        </div>
                        <div className='cart-product-panel-price-actions'>
                            <div className="cart-product-panel-price-quantity">
                                <div className={`cart-product-panel-price-quantity-remove${ quantity > 1 ? '' : ' disabled'}`} onClick={() => {
                                    if (quantity > 1) { removeProduct(product) };
                                }}>
                                    <i className="fa-solid fa-minus"></i>
                                </div>
                                <input className="cart-product-panel-price-quantity-value" type="text" maxLength={ 3 } value={ quantity } />
                                <div className={`cart-product-panel-price-quantity-add${ quantity < product.max_quantity ? '' : ' disabled' }`} onClick={() => {
                                    if (quantity < product.max_quantity) { addProduct(product) };
                                }}>
                                    <i className="fa-solid fa-plus"></i>
                                </div>
                            </div>
                            <div className="cart-product-panel-price-remove" onClick={() => { removeFullProduct(product) }}>
                                <i className="fa-solid fa-xmark"></i>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Element;