import { createContext, useContext, useState } from "react";

import { Product } from '../types';

interface CartProduct extends Product {
    quantity: number;
}

interface CartContextType {
    products: CartProduct[];
    quantity: number;
    price: {
        value: number;
        currency: '₽';
    }
    hasProduct: (product: Product) => boolean;
    getProductQuantity: (product: Product) => number;
    addProduct: (product: Product) => void;
    removeProduct: (product: Product) => void;
    removeFullProduct: (product: Product) => void;
    clear: () => void;
}

const CartContext = createContext<CartContextType | null>(null);

interface Props {
    children: React.ReactNode;
}

export const useCart = () => {
    const context = useContext(CartContext);
    if (!context) throw new Error("useCart must be used within a CartProvider");
    
    return context;
}

export const CartProvider = ({ children }: Props) => {
    const [products, setProducts] = useState<CartProduct[]>([]);
    const quantity = products.reduce((sum, p) => sum + p.quantity, 0);
    const price = {
        value: products.reduce((sum, p) => sum + p.price.value * p.quantity, 0),
        currency: '₽' as const
    };

    const hasProduct = (product: Product) => products.some((p) => p.name === product.name);

    const getProductQuantity = (product: Product) => {
        const found = products.find((p) => p.name === product.name);
        return found ? found.quantity : 0;
    }

    const addProduct = (product: Product) => {
        setProducts((prev) => {
            const existing = prev.find((p) => p.name === product.name);
            if (existing) {
                return prev.map((p) =>
                    p.name === product.name
                        ? { ...p, quantity: p.quantity + 1 }
                        : p
                );
            }
            return [...prev, { ...product, quantity: 1 }];
        });
    }

    const removeProduct = (product: Product) => {
        setProducts((prev) =>
            prev
                .map((p) =>
                    p.name === product.name
                        ? { ...p, quantity: p.quantity - 1 }
                        : p
                )
                .filter((p) => p.quantity > 0)
        );
    }

    const removeFullProduct = (product: Product) => {
        setProducts((prev) => prev.filter((p) => p.name !== product.name));
    };

    const clear = () => setProducts([]);

    return (
        <CartContext.Provider value={{ products, quantity, price, hasProduct, getProductQuantity, addProduct, removeProduct, removeFullProduct, clear }}>
            {children}
        </CartContext.Provider>
    );
}